import $ from "../core"

$.prototype.accordion = function (contentActiveClass = "accordion-content_active", padding = 20) {
    for (let i = 0; i < this.length; i++) {

        const content = $(this[i]).siblings()[0];

        $(this[i]).click(() => {
            $(this[i]).siblings().toggleClass(contentActiveClass);

            if (content.classList.contains(contentActiveClass)) {
                console.log(1);
                content.style.maxHeight = content.scrollHeight + padding + "px";
            } else
                content.style.maxHeight = "0px";
        });
    }
}